import { useState } from 'react';
import { Client } from 'taipa';

class Hint {
    hint: string = ''
    sounds: Array<string> = new Array()
    namesOfSound: Array<string> = new Array()
}

class Entry {
    constructor(public reading: string) {}
}

class Topic {
    entries: Entry[] = [new Entry('hoefcia')]
}


const topic: Topic = new Topic();
const slicedReadings: string[] = []
const literals: string[] = []
let entries: Entry[] = []
const hints: Array<Hint> = new Array()
const highlights: string[] = []
const idx = 0;

let tonalInHanji = new Map()
  .set("f", "一")
  .set("y", "二")
  .set("w", "三")
  .set("x", "五")
  .set("z", "七")
  .set("xx", "九")

let namesInHanji = new Map()
  .set("initial", "初聲")
  .set("medial", "中聲")
  .set("nasalization", "鼻音化")
  .set("stop final", "終聲")
  .set("nasal final", "終聲")
  .set("free tonal", "聲調")
  .set("checked tonal", "聲調")


function getTopic(): void {
    // const id = +this.route.snapshot.paramMap.get('id');
    // this.topicService.getTopic(id).subscribe(topic => this.topic = topic);

    entries = topic.entries

    const clt = new Client()
    for(let i = 0; i < topic.entries.length; i++) {
      let doc = clt.processTonal(topic.entries[i].reading)
      literals[i] = doc.word.literal
      let h = new Hint()
      for(let e of doc.soundSequences) {
        for(let j of e) {
          h.namesOfSound.push(j.name)
          h.sounds.push(j.toString())
        }
        h.hint = namesInHanji.get(h.namesOfSound[0]) + ' ' + h.namesOfSound[0]
      }
      highlights[i] = h.sounds[0]
      let sliced = literals[i].slice(highlights[i].length)
      slicedReadings[i] = sliced
      hints.push(h)
    }
}

function setHintAndHighlight(index: number, n: number) {
    let tonal: string = ""
    if(hints[index].namesOfSound[n] === "free tonal" || hints[index].namesOfSound[n] === "checked tonal") {
        tonal = tonalInHanji.get(hints[index].sounds[n])
        hints[index].hint = namesInHanji.get(hints[index].namesOfSound[n]) + tonal + ' ' + hints[index].namesOfSound[n]
    } else {
        hints[index].hint = namesInHanji.get(hints[index].namesOfSound[n]) + ' ' + hints[index].namesOfSound[n]
    }
    highlights[index] = hints[index].sounds[n]
}

function HintPage() {
    const [input, setInput] = useState('');

    const handleChange = function(e: React.ChangeEvent<HTMLInputElement>) {
        setInput(e.target.value);
    };

    getTopic();

    const str: string = input;

    // if(literals[idx].search(new RegExp(str)) != 0) {}

    let len: number = 0
    if(str.length > 0 && literals[idx].search(new RegExp(str)) != 0) {
        for(let j = 0; j < hints[idx].sounds.length; j++) {
        len += hints[idx].sounds[j].length
        if(len >= str.length) {
            if(len > str.length) {
            setHintAndHighlight(idx, j)
            } else {
            if(j+1 == hints[idx].sounds.length) {
                // last sound
                hints[idx].hint = ""
                highlights[idx] = ""
            } else {
                setHintAndHighlight(idx, j+1)
            }
            }
            break;
        }
        }
    } else if(str.length == 0) {
        setHintAndHighlight(idx, 0)
    }

    if(len == str.length) {
        let sliced = literals[idx].slice(str.length)
        if(literals[idx].search(new RegExp(str)) == 0) {
        if(highlights[idx] != undefined) {
            let slicedTwo = sliced.slice(highlights[idx].length)
            slicedReadings[idx] = slicedTwo
        }
        }
    } else if(len > str.length) {}
    
    const words = ['a', 'bax'];

    return (
        <div>{str}, {literals}
            拍羅馬字, 輸出 hint
            <label>
                <br />
                <input type="text" list="words" value={input} onChange={handleChange} />
                <datalist id="words">
                    {words.map(item => (
                        <option key={item} value={item} />
                    ))}
                </datalist>
            </label>
            <br />
        </div>
    );
}

export default HintPage;
